@media screen and (max-width: 768px) {
  body {
  font-size: 14px;
  }

  h1 { font-size: 24px; }
  h2 { font-size: 22px; }
  h3 { font-size: 20px; }
  h4 { font-size: 18px; }
  h5 { font-size: 16px; }
  h6 { font-size: 14px; }

  nav ul {
  flex-direction: column;
  padding: 0;
  }

  nav ul a {
  padding: 0.5rem 1rem;
  }

  header .container {
  flex-direction: column;
  }

  .collapse {
  transition: max-height 0.3s ease-in-out;
  }

  .error p {
  font-size: 1rem;
  }

  section:not(
    #home-section,
    #introduction,
    #hero-section,
    #about,
    #top-50,
    #experience-book
  ) {
  transform: translateY(100px);
  }

  section {
    padding: 0 15px;
  }

  #home-section .container, #home-section .content, .book-img, .img {
    flex-direction: column;
    align-items: center;
    text-align: center;
  }

  #home-section .content, .book-name, #genre, h2, .pen, .book-img, .btn-wrapper, a.btn {
    width: 100%;
    max-width: none; /* Resets any max-width */
  }

  section .about {
    width: 80%; /* Adjusts width for smaller screens */
    left: 10%; /* Centers the about section */
  }

  .book-img {
    top: 5%; /* Adjusts the position of the book image */
    max-width: 80%; /* Makes the book image smaller */
  }

  .book-name {
    font-size: 2rem; /* Smaller font size for book name */
  }

  #genre, h2 {
    font-size: 1.2rem; /* Smaller font sizes for genre and h2 */
  }

  .pen {
    padding: 0.4rem 1rem; /* Adjusts padding for smaller screens */
  }

  #target .container .user-categories {
    width: 100%; /* Full width for better responsiveness */
    flex-direction: column; /* Stacks the categories vertically */
  }

  #target .container .user-categories li {
    width: 100%; /* Full width for list items */
  }

  #about-book, #experience-book, #about-author, .container-lv-2, #recommended-books, footer {
    padding: 5%;
  }

  #about-book .imgs, #about-author #author-img img, #experience-book .img img, #recommended-books .book img {
    max-width: 100%;
  }

  #about-book iframe, .content, #experience-book .content {
    width: 100%;
    padding-left: 0;
    padding: 1rem;
  }

  .suppliers, .container-lv-2 {
    flex-direction: column;
  }

  .testimonials {
    width: 90%;
    left: 5%;
  }

  #recommended-books .book-list {
    flex-wrap: wrap;
    justify-content: center;
  }

  .footer-content {
    align-items: center;
    text-align: center;
  }

  .social-icons {
    justify-content: center;
  }
}

